import React, { FC } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { AppState, Message } from '../reducers/kid';
import MessageBox from '../components/MessageBox';
import { bindActionCreators, Dispatch } from 'redux';
import { notifyLogin } from '../actions/kid';
import { LoginUser } from '../services/models';
import Login from '../components/Login';

interface StateProps {
    
}

interface DispatchProps {
    notifyLogin: (loginUser: LoginUser) => void;
}

type EnhancedProps = StateProps &
    DispatchProps;

const mapStateToProps = (state: AppState): StateProps => ({
    
});

const mapDispatchToProps = (dispatch: Dispatch): DispatchProps =>
    bindActionCreators(
        {
            notifyLogin: (loginUser: LoginUser) =>
                notifyLogin(loginUser),
        },
        dispatch,
    );

const MessageContainer: FC<EnhancedProps> = ({
    notifyLogin,
}) => {

    return (
        <Login notifyLogin={notifyLogin} />
    );
};

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(MessageContainer);
